import EyeIcon from 'react-icons/lib/fa/eye'
import PuzzleIcon from 'react-icons/lib/fa/puzzle-piece'
import React from 'react'
import {
  Badge,
  Card,
  CardBody,
  Button,
  CardTitle,
  CardText,
  CardImg,
  UncontrolledTooltip,
} from 'reactstrap'
